<script>
    import { getContext } from 'svelte';
    import Popup from './Modal/Popup.svelte';

    const { open } = getContext('simple-modal');
	
    const showPopup = (text) => {
		open(Popup, { message: text});
	};

    async function upload(file) {
        var response = await fetch('./rest/Uploader/uploadTorrentFile', { // Your POST endpoint
            method: 'POST',
            body: file // This is your file object
        });
        if (response.ok) {
            showPopup("File uploaded Successfully.");
        } else {
            console.log("Bad upload") // Handle the error response object
            showPopup("There was an issue with the file upload.");
        }
    };

    function uploadFile() {
        const input = document.getElementById('file');
        const formData = new FormData();
        formData.append('file', input.files[0])
        upload(formData);
    }
</script>

<div class="bg-white shadow overflow-hidden sm:rounded-lg text-center">
    <label for="file">Choose a Torrent file to upload</label>
    <input id="file" name="file" type="file" accept=".torrent,.added"/>
    <button on:click={uploadFile} class="bg-white shadow overflow-hidden sm:rounded-lg" type="submit">Submit</button>
</div>

<div id="dialog"></div>

<style>
    div {
        max-width: 360px;
        margin-top: 15em;
    }

    input {
        width: 100%;
    }

    button {
        padding: 4px 8px;
        margin: auto;
    }
</style>