<script>
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();

  export let index = 0;
  export let active;

  function onClick(event) {
    dispatch("click", event);
  }
</script>

<style>
  .odd {
    background-color: #f7f7f7;
  }

  .active {
    box-shadow: inset 1px 1px 2px rgb(0, 112, 0), inset -1px -1px 2px rgb(0, 112, 0);
  }

  .not-active {
    box-shadow: inset 1px 1px 2px rgb(112, 0, 0), inset -1px -1px 2px rgb(112, 0, 0);
  }

  tr {
    border-radius: 0.2em;
  }

</style>

<tr
  on:click={onClick}
  class={$$props.class}
  class:active={active == true}
  class:not-active={active == false}
  class:odd={index % 2 !== 0}
  class:even={index % 2 === 0}>
  <slot />
</tr>
