<script>
    import Select from 'svelte-select';
    export let loadOptions;
    export let optionIdentifier;
    export let getSelectionLabel;
    export let getOptionLabel;
    export let Item;
    export let handleSelect;
    export let placeholder;
    let seasonFrom = 0;
    export let submit;
</script>
  
<style>
    div.container {
        height: 75vh;
        margin-top: 2em;
    }

    button {
        color: #5c5c5c;
        padding: 0px 6px;
    }

    label {
        margin-top: 1em;
    }
</style>
  
<div class="container bg-white shadow sm:rounded-lg justify-center">
    <Select {loadOptions} {optionIdentifier} {getSelectionLabel} {getOptionLabel} {Item} on:select={handleSelect} {placeholder}></Select>

    <div class="">
        <label for="seasonFrom">From Season</label>
        <input id="seasonFrom" type="number" min="0" step="1" bind:value={seasonFrom}/>

        <button on:click={() => {submit(seasonFrom)}}>Submit</button>
    </div>
</div>