<script>
    import { onMount } from "svelte";
    import Table, { Pagination, Row, Search, Sort } from "../Table/Table.svelte";
    import Add from "../Add.svelte";

    export let getActive;
    export let addRecord;
    export let page;
    
	let pageIndex = 0; //first row
    export let pageSize; //optional, 10 by default

    export let loading;
    export let rowsCount;
    export let rows;

    export let text;
    export let sorting;
    export let update;

    onMount(async () => {
        console.log(addRecord);
        await getActive(page);
    });

    function onPageChange(event) {
        getActive(event.detail.page);
		page = event.detail.page;
    }

    async function onSearch(event) {
        text = event.detail.text;
        page = 0;
        await getActive(page);
    }

    function onCellClick(row) {
        console.log("Do Something");
        console.log(row);
        row.active = !row.active;
        update(row);
    }

    async function onSort(event) {
        sorting = { dir: event.detail.dir, key: event.detail.key };
        await getActive(page);
    }
</script>

<Table {loading} {rows} {pageIndex} {pageSize} let:rows={rows2}>
    <div slot="top">
      <div on:click={addRecord}>
        <Add fill="rgb(55, 182, 221)" height="30px" width="30px" clazz="add-button"/>
      </div>
    </div>
    <thead slot="head">
      <tr>
        <th></th>
        <th>
            Title
           <Sort key="title" on:sort={onSort} />
        </th>
        <th>
            Year
            <Sort key="year" on:sort={onSort} />
        </th>
        <th>
            Active
            <Sort key="active" on:sort={onSort} />
        </th>
      </tr>
    </thead>
    <tbody>
        {#each rows2 as row, index (row)}
        <Row active={row.active} {index} on:click={() => onCellClick(row)}>
