            <td data-label="Title">{row.title}</td>
            <td data-label="Year">{row.year}</td>
            <td data-label="Active">{row.active}</td>
        </Row>
      {/each}
    </tbody>
    <div slot="bottom">
      <Pagination {page} {pageSize} count={rowsCount} serverSide={true} on:pageChange={onPageChange} />
    </div>
  </Table>

  <style>
    img {
        width: 30px;
        display: inline;
    }

    th {
        text-align: left;
    }

    td {
