<script>
    import Item from './SeriesItem.svelte';
    import loadOptions from './series.js';
    import Popup from '../Modal/Popup.svelte';
    import SearchPopup from './SearchPopup.svelte';
    import { getContext } from 'svelte';
    import TrackingTable from './TrackingTable.svelte';

    const { open } = getContext('simple-modal');
	
    const optionIdentifier = 'imdbID';
    const getOptionLabel = (option) => option.Title;
    const getSelectionLabel = (option) => option.Title;

    let loading = true;
    let rowsCount = 0;
    let rows = [];
    let page = 0; //first page
    let pageSize = 10;
    let text = "";
    let sorting = "";
    let selected;

    function handleSelect(event) {
        console.log(event.detail);
        var item = {
            "imdbID" : event.detail.imdbID,
            "posterUrl": event.detail.Poster,
            "title" : event.detail.Title,
            "year" : event.detail.Year,
            "active" : true,
            "startPoll" : true
        };
        selected = item;
        //insert(item);
    }

    function submit(seasonFrom) {
        console.log("SeasonFrom :" + seasonFrom);
        selected.seasonFrom = seasonFrom;
        insert(selected);
    }

    async function insert(item) {
        var response = await fetch('./rest/Tracker/insertNewTrackingSeries', { // Your POST endpoint
            method: 'POST',
            body: JSON.stringify(item), // This is your file object
            headers: {
                'Content-Type': 'application/json'
            },
        });
        if (response.ok) {
            showPopup("Series added to AutoPoll.");
        } else {
            console.log("Issue with uploading file")
            showPopup("There was an issue adding the Series to AutoPoll.");
        }
        getActive(page)
    };

    const showPopup = (data) => {
		open(Popup, { message: data});
    };
    
    function addRecord() {
        open(SearchPopup, { 
            "loadOptions": loadOptions, 
            "optionIdentifier": optionIdentifier, 
            "getSelectionLabel": getSelectionLabel, 
            "getOptionLabel": getOptionLabel, 
            "Item": Item, 
            "handleSelect": handleSelect, 
            "placeholder": "Add Series to Poll", 
            "submit": submit
        });
    }

    async function getActive(_page) {
        console.log("Getting page :" + page);
        loading = true;
        const url = new URL('./rest/Tracker/getAllActivePolls', location.href);
        var params = {"index" : pageSize * page, "pageSize" : pageSize};
        if (text && text.length > 0) {
            params.search = text;
        }
        if (sorting && sorting.key) {
            params.sorting = sorting.key + " " + sorting.dir;
        }

        url.search = new URLSearchParams(params).toString();
        const res = await fetch(url);

        loading = false;

		if (res.ok) {
            const json = await res.json();
            rows = json.data;
            rowsCount = json.total;
			return json;
		} else {
			throw new Error(await res.text());
		}

        /* const json = [
