                "title":"The Stand2",
                "year":"2020-2021",
                "active":false,
                "folderName":"/mnt/raid/Videos/Series/The Stand2 2020",
                "startPoll":false,
                "activeDownloads":[],
                "seasonMap":{}
            },
            {
                "id":171,
                "imdbID":"tt1831805",
                "posterUrl":"https://m.media-amazon.com/images/M/MV5BMjNjNDdhZGUtZWZmZS00ZDk3LTg0MzQtZmU5MDExY2Q1ZjYzXkEyXkFqcGdeQXVyNjEwNTM2Mzc@._V1_SX300.jpg",
                "title":"The Stand3",
                "year":"2020-2021",
                "active":true,
                "folderName":"/mnt/raid/Videos/Series/The Stand3 2020",
                "startPoll":false,
                "activeDownloads":[],
                "seasonMap":{}
            }
        ] */
    }
    
    async function update(item) {
        var response = await fetch('./rest/Tracker/updateAutoPollSeries', { // Your POST endpoint
            method: 'POST',
            body: JSON.stringify(item), // This is your file object
            headers: {
                'Content-Type': 'application/json'
            },
        });
        if (response.ok) {
        } else {
            console.log("Issue with uploading file")
            showPopup("There was an issue Updating Series.");
        }
        getActive(page)
    };

</script>
<div class="container bg-white shadow sm:rounded-lg justify-center">
    <TrackingTable {addRecord} {getActive} {loading} {rowsCount} {rows} {page} {pageSize} {text} {sorting} {update}/>
</div>

